<script>
import {fly} from 'svelte/transition'
import RoundedRect from "../primitives/RoundedRect.svelte"

export let markerVisible

let status = 'component loaded'
let isModelDetailVisible = false

AFRAME.registerComponent('model-rotate', {
    init: function(){
        const el = this.el

        this.startingX = null;

        window.addEventListener('touchstart', this.handleTouchStart)
        window.addEventListener('touchmove', this.handleTouchMove,{passive:false})
        window.addEventListener('touchend', this.handleTouchEnd)

               
    },
    handleTouchStart(ev){
        this.startingX = ev.touches[0].clientX;
    },
    handleTouchMove(ev){

        let modelID = document.getElementById('gltf-model')

        let rotationX = modelID.object3D.rotation.x*180/Math.PI
        let rotationY = modelID.object3D.rotation.y*180/Math.PI
        let rotationZ = modelID.object3D.rotation.z*180/Math.PI

        let touch = ev.touches[0];
        let change = this.startingX - touch.clientX;

        if(change < 0 )  {
            rotationZ = rotationZ + 4 // swipe right
            
        } else {
            rotationZ = rotationZ - 4 //swipe left
        }

        modelID.object3D.rotation.set(
            THREE.Math.degToRad(rotationX),
            THREE.Math.degToRad(rotationY),
            THREE.Math.degToRad(rotationZ)
        );

        console.log(rotationZ)


        ev.preventDefault()
        
    },
    handleTouchEnd(ev){

    },

})

AFRAME.registerComponent('autoscale', {
  schema: {type: 'number', default: 1},
  init: function () {
    this.scale();
    this.el.addEventListener('object3dset', () => this.scale());
  },
  scale: function () {
    const el = this.el;
    const span = this.data;
    const mesh = el.getObject3D('mesh');

    if (!mesh) return;

    // Compute bounds.
    const bbox = new THREE.Box3().setFromObject(mesh);

    // Normalize scale.
    const scale = span / bbox.getSize().length();
    mesh.scale.set(scale, scale, scale);

    // Recenter.
    const offset = bbox.getCenter().multiplyScalar(scale);
    mesh.position.sub(offset);
  }
});

const modelExt = "glb"
const objModelUrl = "obj: url(./static/models/chair/jean_obj.obj); mtl: url(./static/models/chair/jean_obj.mtl)"
